.Cover {
    background-image: -webkit-gradient(linear, left top, left bottom, from(rgba(0,0,0, 0.7)), to(rgba(0,0,0,0.7))), url('../../assets/images/hero.jpeg');
    background-image: -webkit-linear-gradient(rgba(0,0,0, 0.7), rgba(0,0,0,0.7)), url('../../assets/images/hero.jpeg');
    background-image: -o-linear-gradient(rgba(0,0,0, 0.7), rgba(0,0,0,0.7)), url('../../assets/images/hero.jpeg');
    background-image: linear-gradient(rgba(0,0,0, 0.7), rgba(0,0,0,0.7)), url('../../assets/images/hero.jpeg');
    background-size: cover;
    background-position: center;
    background-attachment: fixed;
    height: 100vh
}


.HeroTextBox {
    position: absolute;
    width: 100%;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    padding-left: 4%;
}



